.App {
  text-align: center;
  background-color: #a6cfe2;
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

#calc-div {
  background-color: #0b0c11;
  height: 420px;
  width: 300px;
  padding: 5px;
  box-sizing: border-box;
  display: grid;
  display: -ms-grid;
  grid-template-columns: auto auto auto auto;
  -ms-grid-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: auto auto auto auto auto auto;
  -ms-grid-rows: 1fr 1fr 1fr 1fr 1fr 1fr;
  box-shadow: 1px 2px 10px rgba(0, 0, 0, 0.5);
  border-radius: 5px;
}

#display {
  grid-column: 1 / 5;
  grid-row: 1;
  -ms-grid-rows: 1;
  -ms-grid-columns: 1;
  -ms-grid-column-span: 4;
  color: #fff;
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
  padding-bottom: 10px;
  font-size: 26px;
}

.buttons {
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #000;
  border-radius: 5px;
  cursor: pointer;
  user-select: none;
  opacity: 0.9;
}

.buttons:hover {
  border: 1px solid #91979c;
  opacity: 1;
}

#clear {
  grid-column: 1 / 3;
  grid-row: 2;
  -ms-grid-row: 2;
  -ms-grid-column: 1;
  -ms-grid-column-span: 2;
  background-color: #a5243d;
}

#divide {
  grid-column: 3;
  grid-row: 2;
  -ms-grid-row: 2;
  -ms-grid-column: 3;
  background-color: #798086;
}

#multiply {
  grid-column: 4;
  grid-row: 2;
  -ms-grid-row: 2;
  -ms-grid-column: 4;
  background-color: #798086;
}

#seven {
  grid-column: 1;
  grid-row: 3;
  -ms-grid-row: 3;
  -ms-grid-column: 1;
  background-color: #42464a;
}

#eight {
  grid-column: 2;
  grid-row: 3;
  -ms-grid-row: 3;
  -ms-grid-column: 2;
  background-color: #42464a;
}

#nine {
  grid-column: 3;
  grid-row: 3;
  -ms-grid-row: 3;
  -ms-grid-column: 3;
  background-color: #42464a;
}

#subtract {
  grid-column: 4;
  grid-row: 3;
  -ms-grid-row: 3;
  -ms-grid-column: 4;
  background-color: #798086;
}

#four {
  grid-column: 1;
  grid-row: 4;
  -ms-grid-row: 4;
  -ms-grid-column: 1;
  background-color: #42464a;
}

#five {
  grid-column: 2;
  grid-row: 4;
  -ms-grid-row: 4;
  -ms-grid-column: 2;
  background-color: #42464a;
}

#six {
  grid-column: 3;
  grid-row: 4;
  -ms-grid-row: 4;
  -ms-grid-column: 3;
  background-color: #42464a;
}

#add {
  grid-column: 4;
  grid-row: 4;
  -ms-grid-row: 4;
  -ms-grid-column: 4;
  background-color: #798086;
}

#one {
  grid-column: 1;
  grid-row: 5;
  -ms-grid-row: 5;
  -ms-grid-column: 1;
  background-color: #42464a;
}

#two {
  grid-column: 2;
  grid-row: 5;
  -ms-grid-row: 5;
  -ms-grid-column: 2;
  background-color: #42464a;
}

#three {
  grid-column: 3;
  grid-row: 5;
  -ms-grid-row: 5;
  -ms-grid-column: 3;
  background-color: #42464a;
}

#equals {
  grid-column: 4;
  grid-row: 5 / 7;
  -ms-grid-row: 5;
  -ms-grid-column: 4;
  -ms-grid-row-span: 2;
  background-color: #466995;
}

#zero {
  grid-column: 1 / 3;
  grid-row: 6;
  -ms-grid-row: 6;
  -ms-grid-column: 1;
  -ms-grid-column-span: 2;
  background-color: #42464a;
}

#decimal {
  grid-column: 3;
  grid-row: 6;
  -ms-grid-row: 6;
  -ms-grid-column: 3;
  background-color: #42464a;
}

button {
  background: none;
  color: inherit;
  border: none;
  padding: 0;
  font: inherit;
  cursor: pointer;
  outline: inherit;
}
